.Menu {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: inline-block;
  height: 100%;
}

.Menu .menu_trigger {
  display: none;
  cursor: pointer;
}

.Menu .closeMenu {
  background-image: url('../../img/close button.png');
  background-size: 100% 100%;
  position: absolute;
  cursor: pointer;
  display: none;
  right: 1em;
  top: 1em;
  height: 24px;
  width: 24px;
}

.Menu ul {
  -ms-flex-align: center;
      align-items: center;
  color: white;
  display: -ms-flexbox;
  display: flex;
  margin: 0;
  padding: 0;
  height: 100%;
}

.Menu ul li{
  list-style: none;
  margin: 0.5em 0;
  cursor: pointer;
  text-align: center;
  min-width: 120px;
}

.Menu ul li a,
.Menu ul li a:hover,
.Menu ul li a:visited {
  color:inherit;
  text-decoration: none;
}

.Menu .galleryItem:hover {
  position: relative;
  top: 57px;
}

.Menu .galleryItem:hover > .galleryMenu {
  display: -ms-flexbox;
  display: flex;
}

.Menu .galleryMenu {
  background: -webkit-gradient(linear, left top, right bottom, from(rgba(37, 42, 48, 0.95)), to(#000));
  background: -webkit-linear-gradient(top left, rgba(37, 42, 48, 0.95), #000);
  background: -o-linear-gradient(top left, rgba(37, 42, 48, 0.95), #000);
  background: linear-gradient(to bottom right, rgba(37, 42, 48, 0.95), #000);
  color: white;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 0 auto;
  height: auto;
  font-family: 'Avenir Next Ultra';
  display: none;
  position: relative;
  top: 10px;
}

.Menu .galleryMenu li {
  cursor: pointer;
}
.Menu .galleryMenu li:hover {
  color: var(--accent-color)
}

@media screen and (max-width: 600px) {
  .Menu .menu_trigger {
    display: block;
  }

  .Menu .menuMobile {
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    padding: 0 1em;
  }

  .Menu ul {
    -ms-flex-align: center;
        align-items: center;
    background-color: rgba(0, 0, 0, 0.9);
    display: none;
    -ms-flex-direction: column;
        flex-direction: column;    
    height: 100%;
    -ms-flex-pack: center;
        justify-content: center;
    left: 0;
    text-align: center;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 100;
  }

  .Menu ul.show,
  .Menu .closeMenu {
    display: -ms-flexbox;
    display: flex;
  }
}


@media (-webkit-min-device-pixel-ratio: 2),
(-o-min-device-pixel-ratio: 2/1),
(min-resolution: 192dpi) {
  .Menu .closeMenu {
    background-image: url('../../img/close button@2x.png');
  }
}


